




































import Vue from "vue";
import { Component, Mixins, Prop } from "vue-property-decorator";
import "./style/index.scss";
import "@/theme/transition.scss";
import { AnyObject } from "../../types";
import { on, off } from "../../utils/dom";
import Popper from "../../utils/vue-popper";

@Component({
  name: "Popover"
})
export default class Popover extends Mixins(
  Vue,
  Popper
) {
  static componentName = "YnPopover";
  @Prop({
    type: String,
    default: "click"
  })
  trigger!: string
  @Prop({
    type: String,
    default: "fade-in-linear"
  })
  transition!: string;
  @Prop({
    type: String,
    default: ""
  })
  title!: string;
  @Prop({
    type: String,
    default: ""
  })
  popperClass!: string;
  get style() {
    const style: AnyObject = {};
    if (this.width) style.width = this.width + "px";
    return style;
  }

  toggle() {
    this.$emit("update:visible", !this.visible);
    console.log("toggle");
  }
  handleDocumentClick(e: Event) {
    const domPop = this.$refs.popper as HTMLElement;
    const domReference = this.$refs.reference as HTMLElement;
    const target = e.target as HTMLElement;
    if (!this.$el || this.$el.contains(target) ||
      !domPop || domPop.contains(target) ||
      !domReference || domReference.contains(target)) {
        //  popoverpopover
      return;
    } else {
      this.$emit("update:visible", false);
    }
  }
  handleMouseEnter() {
    this.$emit("update:visible", true);
  }
  handleMouseLeave() {
    this.$emit("update:visible", false);
  }
  beforeEnter() {
    this.$emit("beforeEnter");
  }
  afterEnter() {
    this.$emit("opend");
  }
  beforeLeave() {
    this.$emit("beforeLeave");
  }
  afterLeave() {
    this.$emit("close");
  }
  mounted() {
    let reference = this.$refs.reference as HTMLElement;
    let poper = this.$refs.popper as HTMLElement;
    switch(this.trigger) {
      case "click": {
        on(reference, "click", this.toggle);
        on(document, "click", this.handleDocumentClick);
        break;
      }
      case "hover": {
        on(reference, "mouseenter", this.handleMouseEnter);
        on(poper, "mounseenter", this.handleMouseEnter);
        on(reference, "mouseleave", this.handleMouseLeave);
        on(poper, "mounseleave", this.handleMouseLeave);
        break;
      }
    }
  }
  beforeDestroy() {
    let reference = this.$refs.reference as HTMLElement;
    off(reference, "click", this.toggle);
    off(document, "click", this.handleDocumentClick);
  }
}
