







































































































































import Vue from "vue";
import "./style/index.scss";
import { Component } from "vue-property-decorator";
import { AnyObject } from "../../src/types";
const data = require("./ykb_hot_city.json").result;
const alphaBetData = require("./ykb_alphabat_city.json").result;
@Component({
  name: "cityPickerPage"
})
export default class CityPickerPage extends Vue {
  currentNum = 1;
  keywords = "";
  alphaBetData: AnyObject = alphaBetData;
  data: AnyObject = data;
  defalutCityName1 = "";
  defalutCityName2 = "";
  defalutCityName3 = "";
  defalutCityName4 = "";
  defalutCityName5 = "";
  defalutCityName6 = "";
  defalutCityName7 = "";
  defalutCityName8 = "";
  defalutCityName9 = "";
  defalutCityName10 = "";
  picker1 = false;
  picker2 = false;
  picker3 = false;
  picker4 = false;
  picker5 = false;
  picker6 = false;
  picker7 = false;
  picker8 = false;
  picker9 = false;
  picker10 = false;
  tabs1: AnyObject[] = [{ label: "", key: "mainland-china" }];
  tabs2: AnyObject[] = [{ label: "/", key: "overseas" }];

  handleAfterLeave() {
    (this.$refs.cityPicker as AnyObject).clearSearchKeywords();
  }
  parse(city: AnyObject, nameSpace: string) {
    if (nameSpace === "search-result") {
      const str = `<span>
                      <span class="hightlight-city"></span>
                      <span class="city-province">${city.CityName}-${city.CountryName}</span>
                      <span class="distance"></span>
                  </span>`;
      const innerHTML = `${str.replace(
            new RegExp(this.keywords, "ig"),
            `<i>${this.keywords}</i>`
          )}`;
      return innerHTML;
    } else {
      const str = `<span>${nameSpace} ${city.CityName}</span>`;
      return str;
    }
  }
  getHistory() {
    return {
      title: "",
      params: { a: "1", b: "2" },
      action: this.getHistoryAction,
      parse: (e: AnyObject, params: AnyObject = {}) => {
        const { tab } = params;
        if (tab === "mainland-china") {
          return e.cnCitiesList;
        } else if (tab === "overseas") {
          return e.intCitiesList;
        }
      }
    };
  }
  getHistoryAction(params = {}) {
    console.log("", params);
    return new Promise((resolve, reject) => {
      console.log(reject);
      // setTimeout(() => {
      //   reject("");
      // }, 1000);
      setTimeout(() => {
        resolve(data);
      }, 1000);
    }).catch(err => {
      // this.Toast(err);
      console.log(err, "");
    });
  }
  handlePick(e: AnyObject) {
    // this.Toast(`${e.CityName}`);
    console.log("", e.CityName);
    (this as AnyObject)[`defalutCityName${this.currentNum}`] = e.CityName;
  }
  handlePicker(num: number) {
    this.currentNum = num;
    (this as AnyObject)[`picker${num}`] = !(this as AnyObject)[`picker${num}`];
  }
  transferAlphabetData(params: AnyObject, data: AnyObject) {
    const { alphaBeta } = params;
    const alphaBetaSome = alphaBeta.split("");
    const transferedData: AnyObject = {};
    alphaBetaSome.map((key: string) => {
      transferedData[key] = data[key];
    });
    return transferedData;
  }
  getCityListByAlphaBeta(params: AnyObject = {}) {
    console.log("", params);
    
    return new Promise((resolve, reject) => {
      console.log(reject);
      // setTimeout(() => {
      //   reject("");
      // }, 1000);

      setTimeout(() => {
        resolve(this.transferAlphabetData(params, alphaBetData));
      }, 1000);
    }).catch(err => {
      // this.Toast(err);
      console.log(err, "");
    });
  }
  getHotCityAction(params = {}) {
    console.log("", params);
    return new Promise((resolve, reject) => {
      // setTimeout(() => {
      //   reject("");
      // }, 1000);
      console.log(reject);
      setTimeout(() => {
        resolve(data);
      }, 1000);
    }).catch(err => {
      // this.Toast(err);
      console.log(err, "");
    });
  }
  getSearchAction(params: AnyObject = {}) {
    console.log("", params);
    const { value } = params;
    this.keywords = value;
    return new Promise((resolve, reject) => {
      console.log(reject);
      // setTimeout(() => {
      //   reject("");
      // }, 1000);
      setTimeout(() => {
        resolve(data);
      }, 1000);
    }).catch(err => {
      // this.Toast(err);
      console.log(err, "");
    });
  }
  getSearch() {
    return {
      params: { a: "1", b: "2" },
      action: this.getSearchAction,
      parse: (e: AnyObject, params: AnyObject = {}) => {
        const { tab } = params;
        if (tab === "mainland-china") {
          return e.cnCitiesList;
        } else if (tab === "overseas") {
          return e.intCitiesList;
        }
      }
    };
  }
  getHotCity() {
    return {
      title: "",
      params: { a: "1", b: "2" },
      action: this.getHotCityAction,
      parse: (e: AnyObject, params: AnyObject = {}) => {
        const { tab } = params;
        if (tab === "mainland-china") {
          return e.cnCitiesList;
        } else if (tab === "overseas") {
          return e.intCitiesList;
        }
      }
    };
  }
  getAlphaBeta() {
    return {
      title: "",
      params: { a: "1", b: "2" },
      action: this.getCityListByAlphaBeta,
      parse: (e: AnyObject) => {
        return e;
      }
    };
  }
}
